import { getters as gettersHelpers, sortDate } from "./helpers";
import gettersImported from "./getters";
import events from "@/data/events.js"
import chipsMutation from "./chips"
import moment from 'moment-business-days';
import _ from "lodash";
import SmartSorting from "./smart-sorting";
import { mutationsDays } from "./days";
import { groupedEvents, reOrdering } from './order'

const state = () => ({
    viewType: '5day',
    day: moment().format("YYYY-MM-DD"), // current day --- need to remove at the end
    events,
    tagAllEvents: false
})

// getters
const getters = {
    ...gettersImported,
    ...gettersHelpers
}

// mutations
const mutations = {
    ...chipsMutation,
    ...mutationsDays,
    // remove the event
    REMOVE_EVENT(state, { id, original, refer }) {
        const getEvent = state.events.findIndex((e) => e.id == id);
        if (getEvent == -1) return;
        if (original) {
            if (!confirm('Are you sure you want delete the original event')) return;
            state.events = state.events.filter(e => e.refer != refer)
        } else {
            state.events.splice(getEvent, 1);
        }
    },
    // add new event in array
    ADD_EVENT(state, { id }) {
        const unique = Math.random().toString()
        const newEvent = {
            ...state.events.find((e) => e.id == id),
            id: unique,
            tags: [],
            original: false,
        };
        const day = moment(newEvent.start)
            .businessAdd(1, "days")
            .format("YYYY-MM-DD");
        newEvent.start = day;
        newEvent.end = day;
        const [r, c] = newEvent.locate.split(",");
        newEvent.locate = `${r},${+c + 1}`
        // add the event
        state.events.push(newEvent)
    },
    // Change event date - with related to other events
    CHANGE_EVENT_DATE(state, { id, to }) {
        if (!moment(to).isBusinessDay()) return;
        const getEv = state.events.findIndex((e) => e.id == id);
        if (state.events[getEv].start == to) return;
        const refs = new SmartSorting(state.events).getAllRef(state.events[getEv], true);
        const mainRefIndex = refs.findIndex((r) => r.id == id)
        const prevRefs = refs.slice(0, mainRefIndex)
        const afterRefs = refs.slice(mainRefIndex + 1)
        sortDate(prevRefs).reverse().forEach((e, i) => {
            state.events[e.idx].start = moment(to).businessSubtract(i + 1, "days");
            state.events[e.idx].dragged = true
        })
        sortDate(afterRefs).forEach((e, i) => {
            state.events[e.idx].start = moment(to).businessAdd(i + 1, "days").format("YYYY-MM-DD");
            state.events[e.idx].dragged = true

        })
        state.events[getEv].start = to;
        state.events[getEv].dragged = true
    },
    SETUP(state) {

    },
    // Always run to sort and change the parent
    REFRESH(state) {
        // Grouped events
        const events = state.events.map((e) => ({
            ...e,
            start: moment(e.start).format("YYYY-MM-DD"),
        }));
        let groupedEvents = _.groupBy(events, "start");
        // console.log(groupedEvents);
        // Add location to it
        const locationEVS = Object.values(groupedEvents)
            .map((eventDay, iD) => {
                const sorted = _.sortBy(eventDay, [function (o) { return o.locate ? o.locate.split(",")[0] : o }])
                console.log('sorted', sorted);
                return sorted.map((e, i) => {
                    const date = gettersImported.dynamicDays(state).findIndex((d) => e.start === d) + 1;
                    let row = i + 1;
                    if (e.locate && e.original && date == 0) {
                        row = e.locate.split(",")[0]
                    }
                    const locate = `${row},${date}`;
                    return { ...e, locate: locate };
                });
            })
            .flat(1).sort(function (a, b) {
                return new Date(a.start) - new Date(b.start);
            })
        // Sorting events
        const smartSort = new SmartSorting(locationEVS, gettersImported.dynamicDays(state));
        const finalSort = smartSort.final()
        // console.log('=>', finalSort);
        state.events = finalSort
    },
}

// actions
const actions = {}


export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}