// Smart sorting locations of an events
class SortingSmart {
    events = []
    originals = new Map(); // { originalID: [... cloned], ...}
    locateMind = new Map();

    constructor(events, dd) {
        this.dynamicDays = dd;
        this.events = events.map((e, i) => ({ ...e, idx: i }));
        this.setOriginals()
    }
    
    // Set the originals
    setOriginals() {
        const originalEv = this.events.filter(e => e.original); // original events
        originalEv.forEach((e) => {
            if (!this.originals.has(e.id)) {
                // console.log(e.id, this.getAllRef(e));
                this.originals.set(e.id, this.getAllRef(e))
            }
        })
    }
    // Get all refs of the event
    getAllRef(event, inlcudePassed = false) {
        const refs = [];
        this.events.forEach((e) => {
            e.refer == event.refer && refs.push(e);
        });
        return !inlcudePassed ? refs.filter((e) => e.id != event.id) : refs;
    }
    // get event
    getEventBy(val, key = 'id') {
        return this.events.find((e) => e[key] === val)
    }
    // get location position
    // row = events in day, col = new day
    getLocate(e) {
        return e.locate.split(",")
    }
    // Final results
    final() {
        for (const parent of this.originals.entries()) {
            const origEvent = this.getEventBy(parent[0]);
            const [rowO, colO] = this.getLocate(origEvent);
            for (const event of Object.values(parent[1])) {
                const [row, col] = this.getLocate(event);
                const shouldBeIn = `${rowO},${col}`;
                const eventThere = this.events.find((e) => {
                    const eventSameDay = this.dynamicDays.includes(e.start)
                    return e.locate === shouldBeIn && eventSameDay
                });
                if (shouldBeIn == event.locate) continue;
                // SWAP
                if (eventThere) {
                    const [rowC, colC] = this.getLocate(eventThere)
                    const finalPos = `${row},${colC}`
                    this.events[eventThere.idx].locate = finalPos
                }
                this.events[event.idx].locate = shouldBeIn;
            }
        }
        return this.events;
    }
}

export default SortingSmart