<template>
  <header>
    <div class="row-cal" :style="autoGrid">
      <div
        class="box-calendar"
        v-for="(date,i) in dynamicDays"
        :key="`bb${date}`"
        :class="{ currentDay: isCurrentDay(date), weekendDayHead: isWeekend(date) }"
        :number="i"
      >
        <h3 class="text-overline">{{ moment(date).format("ddd") }}</h3>
        <v-btn class="mb-1" @click="clickDay(date)" :icon="true" elevation="0">
          {{ moment(date).format("D") }}
        </v-btn>
      </div>
    </div>
  </header>
</template>

<script>
import moment from "moment-business-days";
import { mapGetters, mapMutations, mapState } from "vuex";
export default {
  data: () => ({
    moment,
  }),
  methods: {
    ...mapMutations("calendar", ["UPDATE_DAY", "UPDATE_VIEW_TYPE"]),
    clickDay(day) {
      if (this.viewType == "day") return;
      this.UPDATE_DAY(day);
      if (this.viewType == "month") {
        this.UPDATE_VIEW_TYPE("5day");
      } else if (this.viewType == "5day") {
        this.UPDATE_VIEW_TYPE("day");
      }
    },
    isCurrentDay(date) {
      return moment(date).format("YYYY-MM-DD") == moment().format("YYYY-MM-DD");
    },
    isWeekend(date) {
      return !moment(date).isBusinessDay();
    },
  },
  computed: {
    ...mapState("calendar", ["day", "viewType"]),
    ...mapGetters("calendar", ["dynamicDays"]),
    daysView() {
      return this.dynamicDays.length;
    },
    autoGrid() {
      return {
        "grid-template-columns": `repeat(${this.daysView}, minmax(150px, 1fr))`,
      };
    },
  },
};
</script>

<style lang="scss" scoped>
header {
  width: 100%;
  height: auto;
  .row-cal {
    height: 100%;
    width: 100%;
    margin: 0;
    display: grid;
  }
  .box-calendar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    border-bottom: #9e9e9e 1px solid;
    border-top: #9e9e9e 1px solid;
    &:not(:last-child) {
      border-right: #9e9e9e 1px solid;
    }
  }
}
.currentDay {
  height: 100%;
  background: #585858;
}
</style>