<template>
  <VueScroll ref="vs" :ops="scrollOptions">
    <slot></slot>
  </VueScroll>
</template>

<script>
import VueScroll from "vuescroll/dist/vuescroll-native";
export default {
  props: ["dontListen"],
  data: () => ({
    scrollOptions: {
      vuescroll: {
        wheelScrollDuration: 900,
        sizeStrategy: "percent",
        detectResize: true,
      },
      rail: {
        background: "#fff",
        opacity: 0.2,
        size: "6px",
        keepShow: false,
      },
      bar: {
        background: "#fff",
        keepShow: true,
      },
    },
  }),
  mounted() {
    if (this.dontListen) return;
    this.$root.$on("scrollto-vs", this.scrollto);
    this.$root.$on("scrollview-vs", this.scrollIntoView);
    this.$root.$on("scrollby-vs", this.scrollBy);
  },
  beforeDestroy() {
    this.$root.$off("scrollto-vs", this.scrollto);
    this.$root.$off("scrollview-vs", this.scrollIntoView);
    this.$root.$off("scrollby-vs", this.scrollBy);
  },
  methods: {
    scrollto(obj = {}, smooth = 500) {
      this.$refs["vs"].scrollTo(obj, smooth);
    },
    scrollIntoView(el = "#box1-1", smooth = 500) {
      this.$refs["vs"].scrollIntoView(el, smooth);
    },
    scrollBy(obj = {}, smooth = 500) {
      console.log(obj);
      this.$refs["vs"].scrollBy(obj, smooth);
    },
  },
  components: { VueScroll },
};
</script>

