<template>
  <VueScroll class="content-grid">
    <HeaderCalendar />
    <!-- Loop through rows (5day/1day/30day... view) -->
    <div>
      <div
        v-for="row in maxLengthRows"
        :key="`row${row}`"
        class="new-grid"
        :style="autoGrid"
      >
        <div
          :id="`box${row}-${col}`"
          v-for="col in daysView"
          :key="`col${col}`"
          :locate="`${row},${col}`"
          @drop="(e) => dropEvent({ e, dayIdx: col })"
          @dragenter.prevent
          @dragover.prevent
          :class="{ weekendDay: isWeekend(col) }"
        >
          <EventItem
            @click="openMenu"
            v-if="getLocatedEvent(`${row},${col}`)"
            :event="getLocatedEvent(`${row},${col}`)"
            :add="isLastEvent(getLocatedEvent(`${row},${col}`))"
            :remove="
              isLastEvent(getLocatedEvent(`${row},${col}`)) ||
              getLocatedEvent(`${row},${col}`).original
            "
            @drop="ADD_CHIP"
            @cloneNextDay="addNewEvent"
            @remove="removeEvent"
          />
          <div v-else class="empty-event pa-5"></div>
        </div>
      </div>
    </div>
    <!-- Event Menu -->
    <EventMenu
      v-model="eventMenu.status"
      :event="eventMenu.event"
      :eventEl="eventMenu.el"
      @deleteChip="DEL_CHIP"
    />
  </VueScroll>
</template>

<script>
import EventItem from "../event/EventItem.vue";
import EventMenu from "../event/EventMenu.vue";
import eventMenuMixin from "../mixins/event-menu";
import { mapState, mapMutations, mapGetters } from "vuex";
import HeaderCalendar from "./Header.vue";
import VueScroll from "../util/VueScroll.vue";
import moment from "moment-business-days";
export default {
  mixins: [eventMenuMixin],
  methods: {
    ...mapMutations("calendar", [
      "ADD_CHIP",
      "ADD_EVENT",
      "REMOVE_EVENT",
      "CHANGE_EVENT_DATE",
      "DEL_CHIP",
      "REFRESH",
      "onlySortPart",
    ]),
    addNewEvent(e) {
      this.ADD_EVENT(e);
      this.REFRESH();
    },
    removeEvent(e) {
      this.REMOVE_EVENT(e);
      this.REFRESH();
    },
    dropEvent({ e, dayIdx }) {
      const id = e.dataTransfer.getData("idkey");
      const isDragChip = e.dataTransfer.getData("is-chip");
      if (!id || isDragChip) return;
      this.CHANGE_EVENT_DATE({ id, to: this.dynamicDays[dayIdx - 1] });
      e.dataTransfer.setData("idkey", null);
      this.REFRESH();
    },
    isWeekend(idxC) {
      return !moment(this.dynamicDays[idxC - 1]).isBusinessDay();
    },
  },
  computed: {
    ...mapGetters("calendar", [
      "maxLengthRows",
      "dynamicDays",
      "groupedEvents",
      "getLocatedEvent",
      "isLastEvent",
      "sortedEvents",
    ]),
    ...mapState("calendar", ["events", "viewType"]),
    daysView() {
      return this.dynamicDays.length;
    },
    autoGrid() {
      return {
        "grid-template-columns": `repeat(${this.daysView}, minmax(150px, 1fr))`,
      };
    },
  },
  components: { VueScroll, EventItem, EventMenu, HeaderCalendar },
};
</script>

<style lang="scss" scoped>
.content-grid {
  width: 100%;
  height: 100%;
  border: #9e9e9e 1px solid;
  border-top: none;
}
.new-grid {
  display: grid;
  flex-direction: row;
  > * {
    border-bottom: #9e9e9e 1px solid;
    &:not(:last-child) {
      border-right: #9e9e9e 1px solid;
    }
  }
  &:last-child {
    padding-bottom: 25px;
  }
}
</style>