export default {
    data: () => ({
        eventMenu: {
            status: false,
            event: null,
            el: null,
        },
    }),
    methods: {
        // Open event menu
        openMenu({ nativeEvent, event }) {
            if (nativeEvent.target.tagName == "I" || nativeEvent.target.classList.contains('actions-header')) return;
            const open = () => {
                this.eventMenu.event = event;
                this.eventMenu.el = nativeEvent.target;
                requestAnimationFrame(() =>
                    requestAnimationFrame(() => (this.eventMenu.status = true))
                );
            };
            if (this.eventMenu.status) {
                this.eventMenu.status = false;
                requestAnimationFrame(() => requestAnimationFrame(() => open()));
            } else {
                open();
            }
            nativeEvent.stopPropagation();
        },
    }
}