<template>
  <div v-if="event">
    <v-menu
      :value="value"
      @input="toggle"
      :close-on-content-click="false"
      :activator="eventEl"
      offset-x
    >
      <v-card color="grey lighten-4" min-width="350px" flat>
        <v-toolbar :color="event.color" dark>
          <v-btn icon>
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
          <v-toolbar-title v-html="event.name"></v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon>
            <v-icon>mdi-heart</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text class="black--text">
          <!-- Details -->
          <div v-html="event.details" />
          <!-- Chips -->
          <ViewTags
            :tags="event.tags"
            @delete="(t) => $emit('deleteChip', { chip: t, event })"
            close
          />
        </v-card-text>
        <!-- Actions -->
        <v-card-actions>
          <v-btn text color="secondary" @click="toggle(false)"> Cancel </v-btn>
        </v-card-actions>
      </v-card>
    </v-menu>
  </div>
</template>

<script>
import ViewTags from "../tags/ViewTags.vue";
export default {
  props: ["value", "event", "eventEl"],
  methods: {
    toggle(status) {
      this.$emit("input", status);
    },
  },
  components: { ViewTags },
};
</script>

