<template>
  <div
    @click="(e) => $emit('click', { nativeEvent: e, event })"
    class="calendar-event-wrapper"
    :draggable="true"
    @dragstart="dragEvent"
    v-if="event"
  >
    <v-sheet
      :unique="event.id"
      :refer="event.refer"
      class="calendar-event pl-1 dark en-4 white--text"
      :color="event.color"
      @drop="(e) => $emit('drop', { e, event })"
      @dragenter.prevent
      @dragover.prevent
    >
      <div class="heading-info">
        <div>
          <h6 v-text="event.name" />
          <p class="mb-0">
            {{ event.details }}
          </p>
          <!-- <h2>{{ event.original ? "ORIGINAL" : "" }}</h2> -->
          <!-- <h1>{{ event.locate }}</h1> -->
        </div>
        <div class="actions-header">
          <v-btn v-if="remove" @click="$emit('remove', event)" small icon>
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-btn v-if="add" @click="$emit('cloneNextDay', event)" small icon>
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </div>
      </div>
      <ViewTags :tags="event.tags" />
    </v-sheet>
  </div>
</template>

<script>
import ViewTags from "../tags/ViewTags.vue";
export default {
  props: ["event", "add", "remove"],
  data: () => ({
    list: [],
  }),
  methods: {
    dragEvent(e) {
      e.dataTransfer.setData("idkey", this.event.id);
    },
  },
  components: { ViewTags },
};
</script>
<style lang="scss">
.calendar-event-wrapper {
  cursor: pointer;
  height: 100%;
}
.calendar-event {
  height: 100%;
  .heading-info {
    display: flex;
    justify-content: space-between;
  }
  h6 {
    font-size: 16px;
  }
  p {
    font-size: 12px !important;
  }
  .actions-header {
    display: flex;
    overflow: hidden;
    i {
      font-size: 18px !important;
    }
  }
}
.ghostChip {
  display: none;
}
</style>