import moment from "moment";
import { mapMutations } from "vuex";
export default {
    methods: {
        ...mapMutations("calendar", ['UPDATE_DAY']),
        scrollByCenter() {
            setTimeout(() => {
                const widthBox = document.querySelector(".box-calendar").offsetWidth;
                const numberBoxs = +document
                    .querySelector(".currentDay")
                    .getAttribute("number");
                const panel = document.querySelector('.calendar-content .__panel').offsetWidth
                const rightWidth = panel - widthBox;
                const formula = (numberBoxs * widthBox) - (rightWidth/2)
                this.$root.$emit("scrollto-vs", { x: formula }, 0);
            });
        },
        setToday() {
            this.UPDATE_DAY(moment());
            setTimeout(() => {
                if (this.viewType == "month") {
                    this.scrollByCenter();
                }
            });
        },
    }
}