<template>
  <VueScroll :dontListen="true">
    <v-card class="mx-auto tags-wrapper">
      <div class="pa-2">
        <v-switch
          :value="tagAllEvents"
          @change="CHANGE_TAGEVENTS"
          label="All events"
          dense
          inset
        />
      </div>
      <div
        v-for="(group, i) in tagsGroup"
        :key="`tg${i}`"
        class="tag-group mb-2"
      >
        <v-card-title v-text="group.title" />
        <v-divider />
        <v-card-text class="chips-container">
          <v-chip
            v-for="tag in group.tags"
            :color="tag.color"
            :idkey="tag.id"
            :key="tag.id"
            :ripple="false"
            @dragstart="drag"
            :draggable="true"
          >
            {{ tag.content }}
          </v-chip>
        </v-card-text>
      </div>
    </v-card>
  </VueScroll>
</template>

<script>
import VueScroll from "../util/VueScroll.vue";
import tagsGroup from "@/data/tags-group.js";
import { mapMutations, mapState } from "vuex";
export default {
  data: () => ({ tagsGroup }),
  methods: {
    ...mapMutations("calendar", ["CHANGE_TAGEVENTS"]),
    drag(e) {
      const id = e.target.getAttribute("idkey");
      e.dataTransfer.setData("idkey", id);
      e.dataTransfer.setData("is-chip", true);
    },
  },
  computed: {
    ...mapState("calendar", ["tagAllEvents"]),
  },
  components: {
    VueScroll,
  },
};
</script>

<style lang="scss" scoped>
.tags-wrapper {
  height: 100%;
}
.chips-container {
  > * {
    margin: 5px;
  }
}
</style>