<template>
  <v-sheet tile class="header-actions">
    <v-toolbar flat>
      <!-- {{ day }} -->
      <v-btn outlined class="mr-4" color="grey darken-2" @click="setToday">
        Today
      </v-btn>
      <v-spacer></v-spacer>
      <TooltipIcon
        @click="addDays(-1)"
        icon="mdi-chevron-left"
        :hint="`Previous ${hint}`"
      />
      <CalendarPicker />
      <TooltipIcon
        @click="addDays(1)"
        icon="mdi-chevron-right"
        :hint="`Next ${hint}`"
      />
      <v-spacer></v-spacer>
      <ChangeTypeCalendar />
    </v-toolbar>
  </v-sheet>
</template>

<script>
import ChangeTypeCalendar from "../actions/ChangeType.vue";
import TooltipIcon from "../util/TooltipIcon.vue";
import CalendarPicker from "../actions/CalendarPicker.vue";
import { mapMutations, mapState } from "vuex";
import scrollCenter from "../mixins/scrollCenter";
export default {
  mixins: [scrollCenter],
  watch: {
    day() {
      this.REFRESH();
    },
  },
  methods: {
    ...mapMutations("calendar", ["ADD_DAYS", "REFRESH"]),
    addDays(sign) {
      this.ADD_DAYS(sign);
      setTimeout(() => {
        if (this.viewType == "month") {
          sign === -1
            ? this.$root.$emit("scrollto-vs", { x: "100%" }, 0)
            : this.$root.$emit("scrollto-vs", { x: "0%" }, 0);
        }
      });
    },
  },
  computed: {
    ...mapState("calendar", ["day", "viewType"]),
    hint() {
      const hints = {
        day: "day",
        month: "month",
        "5day": "5 day",
      };
      return hints[this.viewType];
    },
  },
  components: {
    TooltipIcon,
    CalendarPicker,
    ChangeTypeCalendar,
  },
};
</script>

<style lang="scss" scoped>
.header-actions {
  display: flex;
  align-items: center;
}
</style>