<template>
  <v-menu bottom right>
    <template v-slot:activator="{ on, attrs }">
      <v-btn outlined color="grey darken-2" v-bind="attrs" v-on="on">
        <span>{{ viewType }}</span>
        <v-icon right> mdi-menu-down </v-icon>
      </v-btn>
    </template>
    <v-list>
      <v-list-item
        v-for="type in types"
        :key="type.value"
        @click="updateView(type)"
      >
        <v-list-item-title v-text="type.label" />
      </v-list-item>
    </v-list>
  </v-menu>
</template>

<script>
import scrollCenter from "../mixins/scrollCenter";
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  mixins: [scrollCenter],
  data: () => ({
    types: [
      { value: "month", label: "month" },
      { value: "5day", label: "5 day" },
      { value: "day", label: "day" },
    ],
  }),
  watch: {
    viewType() {
      this.REFRESH();
    },
  },
  methods: {
    ...mapMutations("calendar", ["UPDATE_VIEW_TYPE", "REFRESH"]),
    updateView(type) {
      this.setToday();
      this.UPDATE_VIEW_TYPE(type.value)
    },
  },
  computed: {
    ...mapGetters("calendar", ["dynamicDays"]),
    ...mapState("calendar", ["viewType", "day"]),
  },
};
</script>

