const inTagAllEvents = (state, event, cb, includes = true, tagId = null) => {
    if (!state.tagAllEvents) return cb(event);
    state.events.forEach(ev => {
        if (ev.refer != event.refer) return;
        if (ev.tags.includes(tagId) && includes) return;
        cb(ev)
    });
}

// CHIPS mutation
export default {
    // Add chip (drop)
    ADD_CHIP(state, { e, event }) {
        const tagId = e.dataTransfer.getData("idkey");
        inTagAllEvents(state, event, (ev) => state.events[ev.idx].tags.push(tagId), true, tagId)
    },
    // Delete chip
    DEL_CHIP(state, { event, chip }) {
        const rmTag = (ev) => {
            const locatedChip = ev.tags.findIndex((t) => t == chip);
            if (locatedChip == -1) return;
            ev.tags.splice(locatedChip, 1);
        }
        inTagAllEvents(state, event, rmTag, false)
    },
    // toggle tag All Events
    CHANGE_TAGEVENTS(state, payload) {
        state.tagAllEvents = !!payload;
    },
}