import moment from 'moment-business-days';
import _ from "lodash";

// first day business
export function firstBussDay(state) {
    var first = moment(state.day).startOf('week');
    return first.day() % 5 === 0 ? first.add(1, 'day').day(1) : first;
}

export default {
    // All events included with sorted date
    // grouped events by date
    groupedEvents(state) {
        const events = state.events.map((e) => ({
            ...e,
            start: moment(e.start).format("YYYY-MM-DD"),
        }));
        let groupedEvents = _.groupBy(events, "start");
        return groupedEvents;
    },
    // the height number of days
    maxLengthRows(state, getters) {
        return _.maxBy(Object.values(getters.groupedEvents), (o) => o.length).length;
    },
    // Days should be added 
    daysAdded(state) {
        const added = { '5day': 5, day: 1, month: moment(state.day).daysInMonth() };
        return added[state.viewType]
    },
    // Dynamic Days from now "view type"
    dynamicDays(state) {
        const added = { '5day': 5, day: 1, month: moment(state.day).daysInMonth() };
        const day = added[state.viewType];
        const dayCalc = (i) => ({
            'day': moment(state.day).businessAdd(i, "days").format("YYYY-MM-DD"),
            '5day': firstBussDay(state).businessAdd(i, "days").format("YYYY-MM-DD"),
            'month': moment(state.day).startOf('month').add(i, "days").format("YYYY-MM-DD")
        })[state.viewType]
        return new Array(day)
            .fill(0)
            .map((_, i) => dayCalc(i));
    },
    // events with locate property -- without sorted
    locatedEvents(state, getters) {
        return Object.values(getters.groupedEvents)
            .map((eventDay, iD) => {
                return eventDay.map((e, i) => {
                    const date = getters.dynamicDays.findIndex((d) => e.start === d) + 1;
                    const locate = `${i + 1},${date}`;
                    return { ...e, locate: locate };
                });
            })
            .flat(1);
    },
    // single find located event (should be in same day)
    getLocatedEvent: (state, getters) => (locate) => {
        return state.events.find((e) => {
            const eventSameDay = getters.dynamicDays.includes(e.start)
            return e.locate === locate && eventSameDay
        }) || null;
    },
}