<template>
  <div class="calendar-picker">
    <v-dialog ref="dialog" v-model="modal" persistent width="290px">
      <template v-slot:activator="{ on, attrs }">
        <div v-bind="attrs" v-on="on">
          <h1 class="text-h6">{{readDay}}</h1>
        </div>
      </template>
      <!-- For Months -->
      <v-date-picker
        v-if="day && modal"
        :value="day"
        @input="UPDATE_DAY"
        :type="type"
        scrollable
        color="primary"
      >
        <v-spacer></v-spacer>
        <v-btn text color="white" @click="modal = false"> Cancel </v-btn>
        <v-btn text color="white" @click="modal = false"> OK </v-btn>
      </v-date-picker>
    </v-dialog>
  </div>
</template>

<script>
import moment from 'moment'
import { mapMutations, mapState } from "vuex";
export default {
  data: () => ({
    modal: false,
  }),
  methods: {
    ...mapMutations("calendar", ["UPDATE_DAY"]),
  },
  computed: {
    ...mapState("calendar", ["viewType", "day"]),
    type() {
      return this.viewType == "month" ? "month" : "date";
    },
    readDay() {
      return moment(this.day).format("MMMM YYYY")
    }
  },
};
</script>

<style lang="scss" scoped>
.calendar-picker {
  display: flex;
  align-items: center;
}
</style>