<template>
  <div class="my-2">
    <div
      v-for="(tagGroup, idx) in allTags"
      :key="`tg${idx}`"
      class="tags-wrapper"
    >
      <v-chip
        @click:close="$emit('delete', tag.id)"
        :close="close"
        v-for="tag in tagGroup"
        :color="tag.color"
        :key="tag.id"
      >
        {{ tag.content }}
      </v-chip>
    </div>
  </div>
</template>

<script>
import tagsGroup from "@/data/tags-group";
export default {
  props: ["tags", "close"], // Array of tags id
  computed: {
    allTags() {
      if (!tagsGroup) return [];
      return tagsGroup
        .map((tg) => tg.tags)
        .map((tg) => tg.filter((tag) => this.tags.find((a) => a == tag.id))).filter((arr) => arr.length);
    },
  },
};
</script>

<style lang="scss" scoped>
.tags-wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 10px;
  &:not(:last-child) {
    margin-bottom: 5px;
    padding-bottom: 5px;
  }
  > *:not(:last-child) {
    margin-right: 10px;
  }
  > * {
    margin: 5px 0;
  }
}
</style>