import moment from "moment";

export const getters = {
    // is last event in refer
    isLastEvent: (state) => (event) => {
        let show = false;
        if (!event) return show;
        let lastEvent = event;
        state.events.forEach((e) => {
            if (e.refer != event.refer) return;
            const date = moment(e.start);
            const now = moment(event.start)
            if (now < date) {
                lastEvent = e;
            }
        });
        if (lastEvent && lastEvent.id == event.id) show = true;
        return show;
    },
}

export const sortDate = (arr) => arr.sort((a, b) => new Date(a.start) - new Date(b.start));
