import gettersImported from "./getters";
import moment from 'moment-business-days';

// Mutatations
export const mutationsDays = {
    ADD_DAYS(state, payload) {
        let daysAdded = gettersImported.daysAdded(state);
        switch (state.viewType) {
            case "month":
                if (payload < 0) {
                    state.day = moment(state.day).startOf('month').subtract(1, 'months').format("YYYY-MM-DD");
                } else {
                    state.day = moment(state.day).startOf('month').add(1, 'months').format("YYYY-MM-DD")
                }
                break;
            case "day":
            case "5day":
                if (payload < 0) {
                    state.day = moment(state.day).businessSubtract(daysAdded, 'days').format("YYYY-MM-DD");
                } else {
                    state.day = moment(state.day).businessAdd(daysAdded, 'days').format("YYYY-MM-DD")
                }
                break;
        }
    },
    UPDATE_DAY(state, payload) {
        state.day = moment(payload).format("YYYY-MM-DD")
    },
    UPDATE_VIEW_TYPE(state, payload) {
        state.viewType = payload
    },
}